.movie {
	width: 40%;
	display: flex;
	margin: 50px 0 50px 0;
	padding: 20px;
	background-color: #ffffff;
	border-radius: 5px;
	box-shadow: 10px 10px 20px 2px rgba(0, 0, 0, 0.1);
}
.movie .movie__poster {
	position: relative;
	top: -40px;
	width: 130px;
	height: 182px;
	box-shadow: 5px 10px 10px 2px rgba(0, 0, 0, 0.4);
}
.movie .movie__data {
	display: flex;
	flex-direction: column;
	padding-left: 30px;
	color: #aeafb8;
	font-size: 0.8em;
}
.movie .movie__data .movie__title {
	font-size: 1em;
	font-weight: bold;
	color: rgb(105, 105, 105);
	padding-bottom: 15px;
}
.movie .movie__data .movie__year {
	padding-bottom: 5px;
}
.movie .movie__data .genres .genres__genre {
	padding-right: 4px;
}

@media screen and (max-width: 900px) {
	.movie {
		width: 80%;
	}
}
@media screen and (max-width: 375px) {
	.movie {
		width: 80%;
		flex-direction: column;
	}
	.movie .movie__data {
		padding-left: 0;
	}
}
